<template>
  <SuccessError v-if="success" type="success" message="Alert created succesfully!" />
    <CardBox v-else>
      <!--FORM-->
            <form @submit.prevent="submitForm">
                    <div class="w-full py-4 flex justify-center text-center inline-flex items-center">
                      <select v-model="selectedCoin" class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500">
                          <option v-for="option in coinOptions" v-bind:value="option.symbol" required>{{ option.name }}</option>
                      </select>
                      <select v-model="selectedMetric" class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500">
                          <option v-for="option in metricOptions" v-bind:value="option.id" required>{{ option.label }}</option>
                      </select>
                      <select v-model="selectedComparator" class=" bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500">
                          <option v-for="option in comparatorOptions" v-bind:value="option" required>{{ option }}</option>
                      </select>
                      <input type="number" v-model="numericalValue" class=" bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required >
                    </div>
                <!--SUBMIT-->
                    <div class="flex justify-center">
                        <button type="submit" class="text-gray-900 bg-white hover:bg-gray-100 border border-gray-200 focus:ring-4 focus:outline-none focus:ring-gray-100 font-medium rounded-lg text-sm px-5 py-2.5 text-center inline-flex items-center dark:focus:ring-gray-600 dark:bg-gray-800 dark:border-gray-700 dark:text-white dark:hover:bg-gray-700 mr-2 mb-2">
                         <img class="w-4 h-4 mr-2" src="https://cdn3.iconfinder.com/data/icons/simple-web-navigation/165/tick-512.png" alt="plus">
                          Create alert
                        </button>
                    </div>
            </form>
      </CardBox>

</template>

<script>
import CardBox from "@/components/CardBox.vue";
import { supabase } from "../supabase";
import { metricOptions } from "@/stores/metricOptions";
import { checkAvailableCoins, getUserInfo, checkAvailableMetrics } from "@/stores/supastore";
import AlertsTable from "@/components/AlertsTable.vue";
import SuccessError from "@/components/SuccessError.vue";

export default {
  components: { 
    CardBox,
    AlertsTable,
    SuccessError
},
  data() {
    return {
      success: false,
      error: null,
      selectedCoin: "BTC",
      coinOptions: [],
      selectedMetric: "price",
      metricOptions: [],
      selectedComparator: ">",
      comparatorOptions: ["=", "<", ">"],
      numericalValue: 0,
      user: {}
    };
  },
  async created() {
    this.user = await getUserInfo();
    this.coinOptions = await checkAvailableCoins();
    this.metricOptions = await checkAvailableMetrics(this.selectedCoin)
  },
  watch: {
    selectedCoin: async function () {
      this.metricOptions = await checkAvailableMetrics(this.selectedCoin)
    },
  },
  methods: {
    async submitForm() {
      try {
        const newAlert = {
          type: "single",
          condition: [
            {
              metric: this.selectedMetric,
              operator: this.selectedComparator,
              metric_value: this.numericalValue,
              first_asset_symbol: this.selectedCoin
            }
          ],
          triggered: 0,
          email: this.user.email,
          user_id: this.user.id
        };

        const { error } = await supabase
          .from("alerts")
          .insert(newAlert);

        this.success = true;
        location.reload();
      } catch (error) {
        console.error(error);
      }
    }
  }
};
</script>
