<template>

        <CardBoxModal v-if="alertToDelete"
          v-model="modalTwoActive"
          title="Confirm deletion"
          button="danger"
          has-cancel
          :on-confirm="deleteAlert"
        >
 
        <p>Are you sure you want to delete the alert for <b>{{ alertToDelete.messari_name || alertToDelete.llama_name || alertToDelete.cmc_name }}</b> (ID {{ alertToDelete.id }})?</p>
        <p> <b>Following rule will be deleted:</b></p>
        <p><small class="text-gray-500 dark:text-slate-400"><i>
          {{ alertToDelete.condition[0].first_asset_symbol }} {{ alertToDelete.condition[0].metric }} is {{ alertToDelete.condition[0].operator }} than {{ alertToDelete.condition[0].metric_value }}
        </i></small></p>
      </CardBoxModal>
<div>
  <table>
    <thead class="text-xs text-gray-700 uppercase dark:text-gray-400">
      <tr>
        <th></th>
        <th>Project</th>
        <th>Rule</th>
        <th>Status</th>
        <th>Date set up</th>
        <th>Date triggered</th>
        <th></th>

      </tr>
    </thead>
    <tbody>
        <tr v-for="alert in alertsPaginated" :key="alert.id">
          <th data-label="Logo" class="w-6 h-6">
              <div class="h-6 w-6">                        
                <img :src="alert.logo ? alert.logo : 'https://cdn4.iconfinder.com/data/icons/leto-weather/64/na_not_available_weather_sun-512.png'" alt="Logo" class="rounded-full flex justify-center align-center content-center">
              </div>
            </th>
                <td data-label="Name" class="w-3/12">
                  <small class="text-gray-500 dark:text-slate-400 text-right">   
                    <router-link :to="`/projects/${alert.symbol}`">
                    {{ (alert.messari_name || alert.llama_name || alert.cmc_name)}}
                  </router-link>
                  </small>
                </td>
                <td data-label="Rule" class="lg:w-1 whitespace-nowrap">
                  <small class="text-gray-500 dark:text-slate-400"> 
                      {{ alert.condition[0].first_asset_symbol }} {{ alert.condition[0].metric }} is {{ alert.condition[0].operator }} than              
                       <p v-html="formatValue(alert.condition[0].metric_value, alert.condition[0].metric)"></p>

                  </small>
                </td>

                <td data-label="Active" class="lg:w-1 whitespace-nowrap">
                  <small class="text-gray-500 dark:text-slate-400"> 
                    {{ alert.triggered === 0 ? "Active" : "Triggered" }}
                  </small>
                </td>
                <td data-label="Date set up" class="lg:w-1 whitespace-nowrap">
                  <small class="text-gray-500 dark:text-slate-400"> 
                    {{ formatDate(alert.created_at) }}
                  </small>
                </td>
                <td data-label="Date triggered" class="lg:w-1 whitespace-nowrap">
                  <small class="text-gray-500 dark:text-slate-400"> 
                    {{ alert.triggered_at === null ? "Not triggered yet" : formatDate(alert.triggered_at) }}
                  </small>
                </td>
                <th data-label="delete" class="h-6 w-6">
                  <div class="h-6 w-6">                        
                    <img src="https://cdn2.iconfinder.com/data/icons/funtime-objects-part-2/60/005_055_delete_cross_close_cancel_exit_vote-512.png" 
                    alt="delete " class="clickable rounded-full" is-hoverable
                    @click="showDeleteModal(alert)">
                  </div>
                </th>
      </tr>
    </tbody>
    <tfoot class="text-xs text-gray-700 uppercase dark:text-gray-400">
      <tr>
        <th></th>
        <th>Coin</th>
        <th>Rule</th>
        <th>Active</th>
        <th>Date set up</th>
        <th>Date triggered</th>
        <th></th>


      </tr>
    </tfoot>
  </table>
      <!--PAGINATION--> 
      <div class="p-3 lg:px-6 border-t border-gray-100 dark:border-slate-800">
        <BaseLevel>
          <BaseButtons>
            <BaseButton class="text-xs text-gray-700 uppercase dark:text-gray-400" label="Previous" @click="currentPage = Math.max(currentPage - 1, 0)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
              <BaseButton
                class="text-xs text-gray-700 uppercase dark:text-gray-400"
                v-for="page in pagesList.slice(Math.max(currentPage - 1, 0), Math.min(currentPage + 1, numPages))"
                :key="page"
                :active="page === currentPage"
                :label="page + 1"
                :color="page === currentPage ? 'lightDark' : 'whiteDark'"
                small
                @click="currentPage = page"
              />
              <BaseButton class="text-xs text-gray-700 uppercase dark:text-gray-400" label="Next" @click="currentPage = Math.min(currentPage + 1, numPages - 1)" :color="page === currentPage ? 'lightDark' : 'whiteDark'"/>
        </BaseButtons>

          <small>Page {{ currentPageHuman }} of {{ numPages }}</small>
        </BaseLevel>
      </div>
</div>
</template>

<script>
import CardBoxModal from "@/components/CardBoxModal.vue";
import BaseLevel from "@/components/BaseLevel.vue";
import BaseButtons from "@/components/BaseButtons.vue";
import BaseButton from "@/components/BaseButton.vue";
import CardBox from "@/components/CardBox.vue";
import { supabase } from "../supabase";
import { getUserInfo } from "@/stores/supastore";
import { formatValue } from "@/stores/supastore";


export default {
  components: {
    CardBoxModal,
    CardBox,
    BaseLevel,
    BaseButtons,
    BaseButton,
  },
  data() {
    return {
      myAlerts: [],
      modalTwoActive: false,
      alertToDelete: null,
      errors: null,
      user: {},
      alertsPaginated: [],
      currentPage: 0,
      perPage: 6,
      metadata: [],
      loaded: false
        };
  },
  async created() {
    this.user = await getUserInfo();
    await this.fetchAlerts();
  },
  watch: {
    currentPage() {
      this.alertsPaginated = this.myAlerts.slice(
        this.perPage * this.currentPage,
        this.perPage * (this.currentPage + 1)
      );
    }
  },
  computed: {
    numPages() {
      if (this.myAlerts.length === 0) {
        return 0;
      }
      return Math.ceil(this.myAlerts.length / this.perPage);
    },
    currentPageHuman() {
      return this.currentPage + 1;
    },
    pagesList() {
      const pagesList = [];
      for (let i = 0; i < this.numPages; i++) {
        pagesList.push(i);
      }
      return pagesList;
    },

  },
  methods: {
    showDeleteModal(alert) {
      this.alertToDelete = alert
      this.modalTwoActive = true
    },
    formatValue(coin, metric) {
      return formatValue(coin, metric)
    },
    async deleteAlert() {
      // Make an API call to Supabase to delete the alert with the specified ID
      const { error } = await supabase
        .from("alerts")
        .delete()
        .eq("id", this.alertToDelete.id)
      console.log("delete alert", this.alertToDelete.id)

      if (error) {
        console.error(error)
      } else {
        location.reload();
      }
    },
    async getAssetMetadata() {
      let { data: metadata, error } = await supabase
        .from("ultimateframe")
        .select("logo, symbol, messari_name, cmc_name, llama_name");
      return metadata;
    },
    async fetchAlerts() {
      // Make HTTP request to Supabase API to get the dashboard data for the user
      try {
        let { data: alerts, error } = await supabase
          .from("alerts")
          .select()
          .eq("user_id", this.user.id)
          .order("created_at", { ascending: false });

        // Get the asset metadata
        const metadata = await this.getAssetMetadata();

        // Loop through the alerts and merge the asset metadata into each alert
        this.myAlerts = alerts.map((alert) => {
          const asset = metadata.find(
            (asset) => asset.symbol === alert.condition[0].first_asset_symbol
          );
          if (asset) {
            return {
              ...alert,
              logo: asset.logo,
              symbol: asset.symbol,
              messari_name: asset.messari_name,
              cmc_name: asset.cmc_name,
              llama_name: asset.llama_name,
            };
          }
          return alert;
        });
        this.updateAlertsPaginated();
      } catch (error) {
        console.error("Following errors were found: ", error);
        this.errors = error.message;
      }
    },
    updateAlertsPaginated() {
      const start = this.perPage * this.currentPage;
      const end = start + this.perPage;
      this.alertsPaginated = this.myAlerts.slice(start, end);
    },
    formatDate(date) {
      if (date) {
        return new Date(date).toLocaleString();
      }
    },
  },
};
</script>

<style>
.clickable:hover {
  cursor: pointer;
}
</style>